.game h1 {
    font-size: 2.5em;
}

.points span {
    font-weight: bold;
}

.tip span {
    color: #ecfa00;
}

.wordContainer {
    margin: 1.5em;
    padding: 1.5em;
    border: 20px solid #ecfa00;
    display: flex;
}

.letter,
.blankSquare {
    font-size: 70px;
    line-height: 1.5;
    border: 3px solid #000;
    height: 100px;
    width: 100px;
    text-transform: uppercase;
    background-color: #fff;
    color: #000;
    font-weight: bold;
}

.letterContainer p{
margin-bottom: 1.2em;
}

.letterContainer form{
display: flex;
align-items: center;
justify-content: center;
}

.letterContainer input{
height: 50px;
width: 50px;
font-size: 2em;
text-align: center;
margin-right: 1em;

}

/* Estilos específicos para dispositivos móveis */
@media screen and (max-width: 768px) {
    .game h1 {
        font-size: 2em;
    }

    .wordContainer {
        margin: 1em;
        padding: 1em;
        border: 17px solid #ecfa00;
        display: flex;
    }

    .letter,
    .blankSquare {
    font-size: 50px;
    line-height: 1.5;
    height: 70px;
    width: 70px;
}

    .letterContainer p{
    margin-bottom: 1em;
    }

    .letterContainer input{
        height: 40px;
        width: 40px;
        font-size: 1.7em;
        text-align: center;
        margin-right: 1em;
        
        }

}

/* Estilos específicos para tablets */
@media screen and (min-width: 768px) and (max-width: 1024px) {
    .game h1 {
        font-size: 1.7em;
    }

    .wordContainer {
        margin: 1em;
        padding: 1em;
        border: 15px solid #ecfa00;
        display: flex;
    }

    .letter,
    .blankSquare {
    font-size: 40px;
    line-height: 1;
    height: 50px;
    width: 50px;
}

    .letterContainer p{
    margin-bottom: 1em;
    }

    .letterContainer input{
        height: 30px;
        width: 30px;
        font-size: 1.5em;
        text-align: center;
        margin-right: 1em;
        
        }

}